.content-tab-wrapper {
    width: 100%;
    height: 91vh;
    margin-top: -30px;
    display: flex;
    justify-content: flex-start;
    .list-product {
      width: 75%;
     
      border-right: 5px solid #ebebeb;
      .list-product-list {
        margin-top: 20px;
        width: 99%;
        max-height: 62vh;
        overflow: auto;
        margin: 0 auto;
        .info-product {
          width: 100%;
          display: flex;
          justify-content: flex-start;
          .info-product__left {
            width: 25%;
            display: flex;
            justify-content: space-around;
            .action-remove:hover{
                cursor:pointer;
            }
          }
          .info-product__center {
            width: 45%;
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
          }
          .info-product__right {
            display: flex;
            justify-content: space-between;
            width: 40%;
            margin-right: 20px;
          }
        }
      }
    }
    .form-sell {
      width: 25%;
      margin-top: 20px;
      .sell-header {
        width: 90%;
        margin: 0 auto;
        display: flex;
        justify-content: flex-start;
        .info-seller {
          width: 70%;
          font-size: 14px;
          color: #807d7d;
        }
        .info-time {
          width: 35%;
          display: flex;
          justify-content: space-between;
          span {
            font-size: 13px;
            color: #cfcfcf;
            border-bottom: 1px solid #cfcfcf;
          }
        }
      }
      .form {
        margin-top: 20px;
        width: 90%;
        margin: 0 auto;
        .search-buyer-search {
          margin-top: 15px;
        }
        .name-bill {
          border-bottom: 1px solid #bababa;
          margin-top: 20px;
          div {
            background-color: #0090da;
            height: 100%;
            width: 90px;
            padding: 5px;
            color: white;
            text-align: center;
          }
        }
        .info-bill {
          margin-top: 20px;
          div {
            margin-top: 15px;
            display: flex;
            justify-content: space-between;
            span {
              font-size: 13px;
            }
          }
          .need-money {
            span {
              font-weight: 700;
            }
          }
        }
        .btn-action {
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
        }
      }
    }
  }
  